#logos{
    text-align:center;
    font-size: 22px;
    font-weight: 600;
    background: white;
    height:32px;
    width: 32px;
    border-radius: 100%;
    margin-left: 100px;
    margin-top: 10px;
    float: left;
   
}
.aSectionn{
    width: 20%;
    float: right;
    font-size: 15px;
    margin-right: 5%;
    margin-top: 1%;
}

.accountt{
    color: white;
    border: 2px solid white;
    padding: 2.5%;
    margin-right: 10px;
}

#loginn{
    border:none;
    margin-top:0px;
    margin-right: 10px;
}
@media screen and (max-width:990px){
    .aSectionn{
        margin-right: 10%;
        margin-top: 1%;
    }
    .accountt{
        display: block;
        width: fit-content;
        margin-left: 15%;
        margin-top: -32px;
    }
    #loginn{
        margin-left:-10%;
       
       
    }
    
    
}
@media screen and (max-width:821px){
   
    .accountt{
        display: block;
        width: fit-content;
        margin-left: 15%;
        margin-top: -35px;
        padding: 1.2%;
        text-align: center;
       
    }
    #loginn{
        margin-left:-15%;
       
       
    }
    
    
}
@media screen and (max-width:806px){
   
    .accountt{
        display: block;
        width: fit-content;
        margin-left: 15%;
        margin-top: -32px;
        padding: 1.6%;
    }
    #loginn{
        margin-left:-15%;
       
       
    }
    
    
}
@media screen and (max-width:558px){
   
    .accountt{
        display: block;
        width: fit-content;
        margin-left: 15%;
        margin-top: -32px;
        padding: 1.0%;
    }
    #loginn{
        margin-left:-18%;
        
    }
   
    
    
}
