body{
    margin: auto;
}
#header{
    background-color:   #ce0505;
    height: 50px;
    width: 100%;
    border: 1px solid black;
    
  
}
#logo{
    text-align:center;
    font-size: 22px;
    font-weight: 600;
    background: white;
    height:32px;
    width: 32px;
    border-radius: 100%;
    margin-left: 100px;
    margin-top: 10px;
    float: left;
   
}
#loginwith{
    font-size: 15px;
    color: white;
    margin-left: 3%;
    margin-top: -30px;
    
    font-family: poppins;
    line-height: 1.56;
}
#accounts{
    font-size: 15px;
    color: white;
    margin-left: 5%;
    margin-top: -30px;
    /* /text-align: center; */
    font-family: poppins;
    line-height: 1.56;
    font-weight: 100;
}
.location{
    border: 1px solid white;
    border-radius: 3px;
    height: 25px;
    width: 150px;
    text-align: center;
    
}


#leftbox{
    height: 600px;
    width: 20%;
    background-color: white;
   
    margin-top: 20px;
    margin-left: 100px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
}

.selectlocation{
    width: 106px;
    height: 20px;
    margin-left: 20px;
    font-family: Poppins;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    text-align: left;
    color: #192f60

}
.dropdown{
    width: 180px;
    height: 30px;
    border-radius: 3px;
    border: solid 1px #8c96ab;
    color: gray;
   
}

.firstimg{
   
    box-shadow: 1px 2px 5px tomato;
    height:350px;
    width:80%;
    float: left;
    margin-left: 2%;
    margin-top: 2%; 
}

.second{
    margin-top: 2%; 
}

.leftopt{
    margin-right: 3%;
}
#filtersome{
    height:1000px;
    width:20%;
    background-color:darkcyan;
    float: left;
    border:5px double black;
    margin-top: 2%; 
    margin-left: 2%; 
}

.firstimg{
    width:70%;
    
}

.image{
    width:50%;
    height:100%;
    float: left;
    margin-top: -20px;
}
.details{
    width:100%;
    height:100%;
}

.maindiv{
    width:100%;
    height:50%;
}

.moredetails{
    width:100%;
    height:30%;
}
.imgClass{
    height:220px;
    width:100%
}
#filtering{
    width:15%;
    box-shadow: 1px 1px 5px #888;
    margin-top: 20px;
}

@media screen and (max-width:1000px){
    #filtering{
        width:12%
    }
    #listingbox{
        /* margin: 0;
        float: right; */
        position: absolute;
        top: 10%;
        left: 15%;
    }
}
@media screen and (max-width:840px){
    #filtering{
        width:14%
    }
    #listingbox{
        /* margin: 0;
        float: right; */
        position: absolute;
        top: 10%;
        left: 15%;
    }
}
@media screen and (max-width:507px){
    #filtering{
        width:15%
    }
    #listingbox{
        /* margin: 0;
        float: right; */
        position: absolute;
        top: 10%;
        left: 15%;
        
    }
}
